import { useTranslation } from "react-i18next";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
import ComicCard from "../ComicCard";
import { forwardRef, useEffect, useRef, useState } from "react";
import { switchName } from "../../../utils/helper";
import { useSelector } from "react-redux";
import { RootState } from "../../../store";

interface ComicMapProps {
  title?: string;
  loadMore?: boolean;
  isLinkEnabled?: boolean;
  toggleSelectItem?: (id: string) => void;
  data: any;
  onReachBottom?: () => void; // Function to load more data
  onCardClick?: () => void;
  lastPage?: boolean;
}

const ComicMap = forwardRef<HTMLDivElement, ComicMapProps>(
  ({
    title,
    loadMore,
    isLinkEnabled,
    toggleSelectItem,
    data,
    onReachBottom,
    onCardClick,
    lastPage = false
  }) => {
    const { t } = useTranslation();
    const [isBottom, setIsBottom] = useState(false);
    const observerRef = useRef<HTMLDivElement | null>(null); // Reference for observing bottom element
    const language = useSelector((state: RootState) => state.init.language);

    useEffect(() => {
      if (!observerRef.current) {
        console.log("Observer ref is not attached");
        return;
      }

      const observer = new IntersectionObserver(
        (entries) => {
          const target = entries[0];
          console.log("Is intersecting:", target.isIntersecting);
          if (target.isIntersecting) {
            setIsBottom(true);
            if (onReachBottom) {
              onReachBottom(); // Trigger the passed-in function
            }
          }
        },
        {
          root: null, // Use the viewport
          threshold: 0.5, // Trigger when the element is half visible
        }
      );

      console.log("Observer attached to:", observerRef.current);
      observer.observe(observerRef.current);

      if (lastPage) {
        observer.unobserve(observerRef.current);
      }

      return () => {
        if (observerRef.current) {
          console.log("Observer detached from:", observerRef.current);
          observer.unobserve(observerRef.current);
        }
      };
    }, [onReachBottom]);

    if (!data || data.length === 0) {
      console.log("No data available to display");
      return null;
    }

    return (
      <div className="comic-map-container">
        <div className="flex px-4 items-end justify-between">
          {title && <h2 className="text-xl font-bold">{title}</h2>}
          {loadMore && (
            <span className="text-xs font-bold pl-4">
              {t("看更多")}
              <FontAwesomeIcon
                icon={faAngleRight}
                className="text-primary-active"
              />
            </span>
          )}
        </div>
        <div className="flex flex-wrap px-2 pb-2">
          {data.map((item: any, index: number) => (
            <div key={index} className="w-1/2 p-1">
              <ComicCard
                id={item._id}
                uuid={item.ci_uuid ? item.ci_uuid : item.uuid}
                title={switchName(item, language)}
                imageUrl={`https:${item.pic_big ?? ""}`}
                isLinkEnabled={isLinkEnabled}
                toggleSelectItem={toggleSelectItem}
                onClick={onCardClick}
              />
            </div>
          ))}
        </div>
        {/* Hidden element to trigger the observer */}
        <div ref={observerRef} className="observer-target h-8"></div>
      </div>
    );
  }
);
export default ComicMap;
