import * as React from "react";
import Box from "@mui/material/Box";
import TabsHeader from "./TabsHeader";
import TabsContent from "./TabsContent";

interface SharedTabsProps {
  tabs: { key: string; label: string; content: React.ReactNode | null }[];
  initialValue?: number;
  className?: string;
  setTabIndex?: (index:number) => void;
}

export default function BasicTabs({ tabs, initialValue = 0, setTabIndex}: SharedTabsProps) {
  const [value, setValue] = React.useState(initialValue);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
    if (setTabIndex) {
      setTabIndex(newValue);
    }
  };

  React.useEffect(() => {
    setValue(initialValue);
  }, [initialValue])

  return (
    <Box sx={{ width: "100%" }}>
      <Box sx={{ borderBottom: 1, borderColor: "divider", paddingX: 2 }}>
        <TabsHeader tabs={tabs} value={value} handleChange={handleChange}  />
      </Box>
      <TabsContent tabs={tabs} value={value} />
    </Box>
  );
}
