import { useTranslation } from "react-i18next";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleRight } from "@fortawesome/free-solid-svg-icons";
import VideoCard from "../VideoCard";
import { forwardRef, useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "../../../store";
import { formatSecondsToHMS, switchName, getCategory } from "../../../utils/helper";
import "./videoMap.css";

interface VideoMapProp {
  title?: string;
  data: any;
  onReachBottom?: () => void;
  isLinkEnabled?: boolean;
  selectItems?: string[];
  toggleSelectItem?: (id: string) => void;
  cate?: string;
  currentPage?: number;
  endPage?: number;
  isSearch?: boolean;
  lastPage?: boolean | null;
}
const VideoMap = forwardRef<HTMLDivElement, VideoMapProp >(
  ({ 
    title, 
    data, 
    onReachBottom, 
    isLinkEnabled, 
    toggleSelectItem, 
    selectItems, 
    cate, 
    currentPage,
    endPage,
    isSearch = false,
    lastPage = false
  }, ref) => {
    
  const { t } = useTranslation();
  const language = useSelector((state: RootState) => state.init.language);
  const init = useSelector((state: RootState) => state.init.init);
  const [isBottom, setIsBottom] = useState(false);
  const observerRef = useRef<HTMLDivElement | null>(null); // 用来监视底部元素的 ref

  useEffect(() => {
    if (!observerRef.current) {
      return;
    }
    
    const observer = new IntersectionObserver(
      (entries) => {
        const target = entries[0];
        if (target.isIntersecting) {
          setIsBottom(true);
          if (onReachBottom) {
            onReachBottom(); // 触发外部传入的函数
          }
        }
      },
      {
        root: null, // 设置监视范围为容器
        threshold: 0.5, // 目标部分可见即可触发
      }
    );

  
    observer.observe(observerRef.current);

    if (lastPage) {
      observer.unobserve(observerRef.current);
    }

    return () => {
      if (observerRef.current) {
        observer.unobserve(observerRef.current);
      }
    };
  }, [onReachBottom, lastPage]); // 仅监听 onReachBottom 变化

  if (!data || data.length === 0) {
    return null;
  }

  return (
    <div
      id="videoMapContainer"
      className="video-map-container "
      ref={ref}
    >
      <div className="flex flex-wrap px-2 pb-2">
        {data?.map((item: any, index: number) => (
          <div key={index} className="w-full p-2">
            <VideoCard
              id={item._id}
              uuid={item.uuid}
              imageUrl={`${init?.img_host}/preview/80/b/${item.uuid}-800.jpg`}
              title={switchName(item, language)}
              preview={`https://tdtest84-ag.qcpro.vip/subpage/getMvPreview.php?uuid=${item.uuid}&v=1722229802`}
              duration={formatSecondsToHMS(item.time)}
              actor={item.actor}
              isLinkEnabled={isLinkEnabled}
              selectItems={selectItems}
              toggleSelectItem={toggleSelectItem}
              cate={getCategory(item) ?? ""}
              mode={"map"}
            />
          </div>
        ))}
      </div>
      {/* 用来监视是否到底的元素 */}
      <div ref={observerRef} className="observer-target h-8"></div>
    </div>
  );
});
export default VideoMap;
