import * as React from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import defaultPng from "../../../images/qc-default-img.jpg";
import { AppDispatch } from "../../../store";
import { useDispatch } from "react-redux";
import { setRecommend2Pay } from "../../../store/analyticsSlice";

interface VideoCardProps {
  id: string;
  uuid: string;
  title: string;
  imageUrl: string;
  preview?: string;
  duration: string;
  actor?: string;
  isLinkEnabled?: boolean;
  selectItems?: string[];
  toggleSelectItem?: (index: string) => void;
  vWidth?: string;
  cate?: string;
  mode?: string;
}

const VideoCard: React.FC<VideoCardProps> = ({
  id,
  uuid,
  title,
  imageUrl,
  preview,
  duration,
  actor,
  isLinkEnabled = true, // 默認啟用連結
  selectItems,
  toggleSelectItem,
  vWidth,
  cate,
  mode
}) => {
  const selectedIndex = selectItems?.indexOf(uuid) ?? -1;
  const isSelected = selectedIndex !== -1;
  const imgRefs = React.useRef<(HTMLImageElement | null)[]>([]);
  const [orientation, setOrientation] = React.useState<'horizontal' | 'vertical'>('vertical');
  const { t } = useTranslation();
  const dispatch:AppDispatch = useDispatch();

  const handleImageLoad = (e: React.SyntheticEvent<HTMLImageElement, Event>) => {
    const img = e.currentTarget;
    if (img.naturalWidth > img.naturalHeight) {
      setOrientation("horizontal");
    } else if (img.naturalWidth < img.naturalHeight) {
      setOrientation("vertical");
    }
  };
  
  React.useEffect(() => {
    if (vWidth !== "vertical") {
      imgRefs.current.forEach((img) => {
        if (img) {
          img.style.objectPosition = "center";
        }
      });
    }
  }, [vWidth]);

  const CardContent = (
    <>
      <div
        className={
          `relative w-full h-full overflow-hidden ${(cate === "歐美" || cate === "欧美") ? "aspect-[0.71]" : "aspect-[1.49]"}`
        }
        id={uuid}
        onClick={() => {
          if (toggleSelectItem) toggleSelectItem(uuid);
        }}
      >
        {/* Overlay if item is selected */}
        {isSelected && (
          <div className="absolute inset-0 bg-gray-500 opacity-50 z-10"></div>
        )}

        {/* Display selected index in a circle at the top-right corner */}
        {isSelected && (
          <span className="absolute top-0 right-0 m-1 bg-blue-500 text-white rounded-full w-6 h-6 flex items-center justify-center z-20">
            {selectedIndex + 1}
          </span>
        )}
        <div 
          className={
            mode === "map" && orientation === "horizontal" && (cate === "歐美" || cate === "欧美")
              ? "w-full relative overflow-hidden pb-[70%]"
              : "w-full h-full relative"
          }
          style={{
            backgroundImage: `url(${defaultPng})`,
            backgroundSize: "cover",
            backgroundPosition: "center",
          }}
        >
          <img
            loading="lazy"
            src={imageUrl}
            alt={title || "Video thumbnail"}
            className="object-cover w-full h-full"
            onLoad={handleImageLoad}
            onError={(e) => {
              (e.target as HTMLImageElement).onerror = null; // 防止無限觸發
              (e.target as HTMLImageElement).src = defaultPng; // 使用本地圖片
              (e.target as HTMLImageElement).style.objectPosition = "center"; // 圖片置中
            }}
            style={ 
              mode === "map" && orientation === "horizontal" && (cate === "歐美" || cate === "欧美")
                ? { 
                    transform: "scale(2.11)", // 放大 1.2 倍 
                    transformOrigin: "top right",
                    display: "block", 
                    width: "100%", 
                    minHeight: 250,
                  }
                : {
                  objectPosition: "right",
                  display: "block", 
                  width: "100%", 
                  
                } 
            }
            ref={(img) => imgRefs.current.push(img)}
          />
        </div>
        {(cate === "強檔獨家" ||
          cate === "獨家" ||
          cate === "强档独家" ||
          cate === "") && (
          <p 
            className="absolute top-0 left-0 m-1 rounded-full text-white text-xxs p-0.5 w-8 h-8 flex items-center justify-center bold-500 border-2"
            style={{
              borderColor: "rgb(5, 247, 72)",
              backgroundColor: "rgb(49, 79, 4)"
            }}
          >
            {t("獨")}
          </p>
        )}
        {(cate === "中文字幕" ||
          cate === t("中文字幕") ||
          cate === "中文") && (
          <p className="absolute top-0 left-0 m-1 rounded-full bg-gray-700 text-fuchsia-500 text-xxs p-0.5 w-8 h-8 flex items-center justify-center bold-500 border-fuchsia-500 border-2">
            {t("中")}
          </p>
        )}
        {(cate === "無碼" || cate === "无码") && (
          <p className="absolute top-0 left-0 m-1 rounded-full bg-gray-700 text-sky-500 text-xxs p-0.5 w-8 h-8 flex items-center justify-center bold-500 border-blue-500 border-2">
            {t("無")}
          </p>
        )}
        {(cate === "自拍") && (
          <p 
            className="absolute top-0 left-0 m-1 rounded-full bg-gray-700 text-xxs p-0.5 w-8 h-8 flex items-center justify-center bold-500 border-lime-500 border-2"
            style={{
              color: "rgb(22, 204, 182)",
              borderColor: "rgb(22, 204, 182)",
            }}
          >
            {t("自")}
          </p>
        )}
        {![
          "强档独家",
          "无码",
          "強檔獨家",
          "獨家",
          "中文字幕",
          "中文",
          "無碼",
          "自拍",
        ].includes(cate || "") && (
          <p className="absolute top-0 left-0 m-1 rounded-full bg-black text-secondary text-xxs p-0.5 w-8 h-8 flex items-center justify-center">
            HD
          </p>
        )}
        <p className="absolute top-0 right-0 m-1 bg-backgroundTrans rounded text-primary-active text-xxs p-0.5">
          {duration || "00:00:00"}
        </p>
      </div>
      <div className="text-left mt-1 text-sm flex-shrink-0 line-clamp-1 break-all">
        {title}
      </div>
      {actor && (
        <div className="text-left mt-1 text-xs flex-shrink-0 line-clamp-1 h-5 text-red-400">
          {actor}
        </div>
      )}
    </>
  );

  return isLinkEnabled ? (
    <Link
      className="h-full w-full flex flex-col justify-start"
      to={`/app/Video?uuid=${uuid}&cate=${cate}&mode=${mode}`}
    >
      {CardContent}
    </Link>
  ) : (
    <div className="h-full w-full flex flex-col justify-between">
      {CardContent}
    </div>
  );
};

export default VideoCard;
