import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation } from "react-router-dom";

import BackBar from "../../components/BackBar";
import VideoMap from "../../components/Video/VideoMap";
import ComicMap from "../../components/Comic/ComicMap";
import BasicModal from "../../components/BasicModal";
import BasicTabs from "../../components/BasicTabs";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEdit, faTimes, faPen, faV } from "@fortawesome/free-solid-svg-icons";

import { RootState, AppDispatch } from "../../store";
import { useSelector, useDispatch } from "react-redux";
import { FavoriteItem, updateFavorite } from "../../utils/pouchdb";
import { setOriginalNode } from "typescript";
import { Favorite, PostParam } from "../../type/favorite";
import { favorite, myFavorite } from "../../store/favoritesSlice";
import { Option } from "../../type/common";
import { showCustomToast } from "../../components/CustomToast";
//import { addFavoriteItem, removeFavoriteItem, updateFavoriteItem, setSelectItem } from "../../store/favoritesSlice";
import Loading from "../../components/Loading";

interface VideoData {
  id: number;
  name: string;
  imageUrl: string;
  duration: string;
}

const FolderVideos: React.FC = () => {
  const { t } = useTranslation();
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const folderName = params.get("name");
  const folderId = params.get("id");

  const dispatch: AppDispatch = useDispatch();

  const folderList = useSelector(
    (state: RootState) => state.favorites.folderList
  );
  const myFavoriteData = useSelector(
    (state: RootState) => state.favorites.myFavoriteData
  );

  const [copyFavorite, setCopyFavorite] = useState<FavoriteItem[]>([]);
  const [isEditing, setIsEditing] = useState<boolean>(false);
  const [selectedItems, setSelectedItems] = useState<string[]>([]);
  const [isModalOpen, setIsModalOpen] = useState<boolean>(false);
  const [folderTitle, setFolderTitle] = useState<string>("");
  const [oriFolderTitle, setOriFolderTitle] = useState<string>("");
  const [folderDescription, setFolderDescription] = useState<string>("");
  const [isMoveModalOpen, setIsMoveModalOpen] = useState<boolean>(false);
  const [selectedFolder, setSelectedFolder] = useState<Option>({
    id: "",
    name: "",
  });
  const [data, setData] = useState<FavoriteItem[]>([]);
  const [folders, setFolders] = useState<FavoriteItem[]>([]);
  const [isDataLoaded, setIsDataLoaded] = useState(false);
  const [currentPageNum, setCurrentPageNum] = useState<number>(1);
  const [allData, setAllData] = useState<Product[]>([]);
  const [comic, setComic] = useState<Product[]>([]);
  const [video, setVideo] = useState<Product[]>([]);

  const handleGetMoreData = () => {
    if (folderId) {
      dispatch(
        myFavorite({
          num: (currentPageNum + 1).toString(),
          folder: folderId
        })
      );
      setCurrentPageNum(currentPageNum + 1);
    }
  }
  const updateAllData = () => {
    if (
      myFavoriteData 
      && currentPageNum !== 1
    ) {
      setAllData((prevAllData) => [...prevAllData, ...myFavoriteData.products]);
    }
  }
  const handleInitAllData = () => {
    if (
      myFavoriteData 
      && currentPageNum === 1
    ) {
      setAllData([]);
      setAllData(myFavoriteData.products);
    }
  };

  useEffect(() => {
    handleInitAllData();
    updateAllData();
  }, [myFavoriteData, folderId])

  useEffect(() => {
    if (folderName) {
      setFolderTitle(folderName);
      setOriFolderTitle(folderName);
    }
  }, [folderName]);

  useEffect(() => {
    if (copyFavorite) {
      const filteredData = copyFavorite.filter(
        (fav) =>
          fav.category === folderTitle &&
          fav.isFolder === false &&
          fav.type === "video"
      );
      setData(filteredData);
      setFolders(copyFavorite.filter((favorite) => favorite.isFolder === true));
    }
  }, [copyFavorite]);

  useEffect(() => {
    console.log(selectedItems);
    if (selectedItems) {
      //dispatch(setSelectItem(selectedItems));
    }
  }, [selectedItems]);

  useEffect(() => {
    if (folderId) {
      dispatch(myFavorite({ folder: folderId })).then(() => {
        setIsDataLoaded(true); // 資料加載完成
      });
    }
  }, [dispatch, folderId]);

  const handleOpenModal = () => setIsModalOpen(true);

  const handleCloseModal = () => setIsModalOpen(false);

  const handleSaveChanges = () => {
    const taFolder = folderList?.data.filter(
      (item) => item.folder_id == folderId
    );
    const config: PostParam = {
      id: taFolder![0].folder_id,
      name: folderTitle,
      memo: folderDescription,
    };
    dispatch(favorite({ method: "modifyFolder", post_param: config })).then(
      (result) => showCustomToast(t("修改成功!"), "error")
    );
    handleCloseModal();
  };

  const toggleEditMode = () => {
    setIsEditing(!isEditing);
    setSelectedItems([]); // 切換編輯模式時清空選中的項目
  };

  const toggleSelectItem = (id: string) => {
    setSelectedItems((prevSelected) =>
      prevSelected.includes(id)
        ? prevSelected.filter((itemId) => itemId !== id)
        : [...prevSelected, id]
    );
  };

  const deleteSelectedItems = () => {
    // console.log("刪除選中的項目:", selectedItems);
    // console.log(selectedItems);
    const config: PostParam = {
      data: JSON.stringify(selectedItems),
      id: folderId ?? "",
    };
    dispatch(favorite({ method: "delVideo", post_param: config }))
      .then((result) => {
        const payload = result.payload as Favorite;
        if (payload.code === -1) {
          dispatch(myFavorite({ folder: folderId ?? "" })).then((result) =>
            showCustomToast(t("刪除成功!"), "error")
          );
        } else {
          console.log("delFolder exception: ", payload);
        }
      })
      .catch((error) => {
        showCustomToast(error, "error");
      });

    setSelectedItems([]);
    setIsEditing(false);
  };

  const openMoveModal = () => setIsMoveModalOpen(true);

  const closeMoveModal = () => setIsMoveModalOpen(false);

  const handleMove = (option: Option) => {
    // console.log("原始資料夾:", `${folderName}-${folderId}`);
    // console.log("移動項目至資料夾:", `${option?.name}-${option?.id}`);
    const data = JSON.stringify(selectedItems);
    const config = {
      old_id: folderId!,
      new_id: option.id,
      data: data,
    };
    dispatch(favorite({ method: "moveVideo", post_param: config }))
      .then((result) => {
        const payload = result.payload as Favorite;
        if (payload.code === -1) {
          dispatch(myFavorite({ folder: folderId ?? "" })).then((result) =>
            showCustomToast(t("移動成功!"), "error")
          );
        } else {
          console.log("delFolder exception: ", payload);
        }
      })
      .catch((error) => {
        showCustomToast(error, "error");
      });

    closeMoveModal();
  };

  useEffect(() => {
    const comicData: Product[] = [];
    const videoData: Product[] = [];

    allData.forEach((item) => {
      if (item?.pic_big) {
        comicData.push(item);
      } else {
        videoData.push(item);
      }
    });
    setComic(comicData);
    setVideo(videoData);
  }, [allData])

  type Product = {
    pic_big?: string;
    [key: string]: any;
  };

  const tabContent = [
    {
      key: "影片",
      label: t("影片"),
      content: isDataLoaded ? (
        <VideoMap
          data={video}
          isLinkEnabled={!isEditing}
          toggleSelectItem={toggleSelectItem}
          selectItems={selectedItems}
          lastPage={Number(myFavoriteData?.pageTotals) === Number(currentPageNum)}
          onReachBottom={handleGetMoreData}
        />
      ) : (
        <Loading customStyle={"h-1/2 mt-40"} />
      ),
    },
    {
      key: "漫畫",
      label: t("漫畫"),
      content: isDataLoaded ? (
        <ComicMap
          data={comic}
          isLinkEnabled={!isEditing}
          toggleSelectItem={toggleSelectItem}
          lastPage={Number(myFavoriteData?.pageTotals) === currentPageNum}
          onReachBottom={handleGetMoreData}
        />
      ) : (
        <Loading customStyle={"h-1/2 mt-40"} />
      ),
    },
  ];

  useEffect(() => {
    console.log("comic: ", comic);
  }, [comic])

  return (
    <div className="relative">
      <BackBar
        customTitle={folderTitle}
        rightComponent={
          <button onClick={handleOpenModal} className="text-white">
            <FontAwesomeIcon icon={faPen} />
          </button>
        }
      />
      <div className="flex justify-end p-4">
        {isEditing && (
          <button onClick={openMoveModal}>
            <FontAwesomeIcon icon={faEdit} />
            <span>{t("移動")}</span>
          </button>
        )}
        <button onClick={toggleEditMode} className="ml-auto">
          <FontAwesomeIcon icon={isEditing ? faTimes : faEdit} />
          <span>{isEditing ? t("取消") : t("編輯")}</span>
        </button>
      </div>

      {/* 使用 VideoMap 組件 */}
      <BasicTabs tabs={tabContent} />

      {/* 編輯標題和註解的彈窗 */}
      <BasicModal
        open={isModalOpen}
        handleClose={handleCloseModal}
        title={t("編輯資料夾")}
        options={[]} // 傳入空陣列
        setSelectedOption={() => {}} // 傳入空函數
        currentState={{ id: "", name: "" }} // 傳入空字串
        content={
          <>
            <label className="block mb-2">{t("資料夾名稱")}</label>
            <input
              type="text"
              value={t(folderTitle)}
              onChange={(e) => setFolderTitle(e.target.value)}
              className="border p-2 w-full mb-4 text-black rounded"
              placeholder={t("輸入資料夾名稱")}
            />
            <label className="block mb-2">{t("註解")}</label>
            <textarea
              value={folderDescription}
              onChange={(e) => setFolderDescription(e.target.value)}
              className="border p-2 w-full mb-4 text-black rounded"
              placeholder={t("輸入註解")}
            ></textarea>
          </>
        }
        onConfirm={handleSaveChanges}
        onCancel={handleCloseModal}
        showButtons={true}
      />

      {/* 移動項目的彈窗 */}
      <BasicModal
        title={t("移動至")}
        options={folderList?.data.map((folder) => ({
          id: folder.folder_id,
          name: folder.folder_name,
        }))}
        open={isMoveModalOpen}
        currentState={selectedFolder}
        handleClose={closeMoveModal}
        setSelectedOption={setSelectedFolder}
        content={""} // 傳入的 content
        onConfirm={() => handleMove(selectedFolder)}
        onCancel={closeMoveModal} // 新增 onCancel 處理函數
        showButtons={true}
      />

      {/* 底部刪除按鈕 */}
      {isEditing && (
        <div className="fixed bottom-0 left-0 w-full bg-background p-4 text-white flex justify-between items-center">
          <span>
            {t("刪除")} ({selectedItems.length})
          </span>
          <button
            onClick={deleteSelectedItems}
            className="bg-red-500 px-4 py-2 rounded-full text-white"
          >
            {t("刪除")}
          </button>
        </div>
      )}
    </div>
  );
};

export default FolderVideos;
