// src/components/CustomToast.tsx
import React from "react";
import { toast, ToastOptions } from "react-hot-toast";

interface CustomToastProps {
  message: string;
  type: "success" | "error" | "loading";
}

const toastStyles: { [key: string]: React.CSSProperties } = {
  success: {
    backgroundColor: "#f44336", // Green background
    color: "#fff",
    paddingLeft: '2rem',
    paddingRight: '2rem',
    marginTop: '50%'
  },
  error: {
    backgroundColor: "#f44336", // Red background
    color: "#fff",
  },
  loading: {
    backgroundColor: "#333", // Dark background
    color: "#fff",
  },
};

export const showCustomToast = (message: string, type: "success" | "error" | "loading") => {
  return toast(message, {
    style: toastStyles[type],
    duration: type === "loading" ? undefined : 4000, // Keep loading toast until dismissed
    position: "top-center",
  });
};