import * as React from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import { colors } from "../../../styles/colors";
import { getFontClass } from "../../../utils/helper";
import { useSelector } from "react-redux";
import { RootState } from "../../../store";

interface TabsHeaderProps {
  tabs: { key: string; label: string; content: React.ReactNode }[];
  value: number;
  handleChange: (event: React.SyntheticEvent, newValue: number) => void;
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    "aria-controls": `simple-tabpanel-${index}`,
  };
}

const TabsHeader: React.FC<TabsHeaderProps> = ({ tabs, value, handleChange }) => {
  const language = useSelector((state: RootState) => state.init.language);
  return (
    <Tabs
      value={value}
      onChange={handleChange}
      aria-label="basic tabs example"
      variant="scrollable"
      scrollButtons="auto"
      TabIndicatorProps={{
        style: {
          backgroundColor: colors.secondary,
        },
      }}
      sx={{
        "& .MuiTab-root.Mui-selected": {
          color: tabs[value].key === "獨家" ? "#02ff00" : "white",
        },
        "& .MuiTabs-indicator": {
          display: "flex",
          justifyContent: "center",
          backgroundColor: "transparent",
          "& > div": {
            width: "100%",
            backgroundColor: "#e8443d",
          },
        },
      }}
    >
      {tabs?.map((tab, index) => (
        <Tab  label={tab.label} key={index} {...a11yProps(index)} 
          sx={{
            color: tab.key === "獨家" ? "#02ff00" : colors.primary.inactive,
            fontSize: "16px",
            fontWeight: "bold",
            minWidth: "auto",
            width: "auto",
            paddingLeft: 0,
            paddingRight: 0,
            paddingBottom: 0,
            marginRight: 4,
          }}
        />
      ))}
    </Tabs>
  );
};

export default TabsHeader;
