import React, { useState, useEffect } from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Modal from "@mui/material/Modal";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimes, faCheck } from "@fortawesome/free-solid-svg-icons";
import { colors } from "../../styles/colors";
import { useTranslation } from "react-i18next";
import Button from "../Button";
import { Option } from "../../type/common";

type BasicModalProps<T> = {
  options?: Option[] | null;
  open: boolean;
  currentState: Option | null;
  handleClose: () => void;
  setSelectedOption: (option: Option) => void;
  title: string;
  content?: React.ReactNode;
  onConfirm: (option: Option | null) => void;
  onCancel: () => void;
  showButtons?: boolean;
};

const modalStyle = {
  position: "absolute" as const,
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: "90%",
  bgcolor: colors.background,
  border: "2px solid #000",
  borderRadius: 2,
  boxShadow: 24,
  p: 2,
  color: colors.primary.active,
  display: "flex",
  flexDirection: "column" as const,
  alignItems: "center",
  justifyContent: "center",
};

function BasicModal<T extends string | number>({
  options,
  open,
  currentState,
  handleClose,
  setSelectedOption,
  title,
  content,
  onConfirm,
  onCancel,
  showButtons,
}: BasicModalProps<T>) {
  const [selectedOption, setSelectedOptionLocal] = useState<Option | null>(currentState);
  const { t } = useTranslation();

  useEffect(() => {
    setSelectedOptionLocal(currentState);
  }, [currentState]);

  const handleOptionSelect = (option: Option) => {
    setSelectedOptionLocal(option);
    setSelectedOption(option);
  };

  return (
    <Modal
      sx={{ maxWidth: 480, margin: "0 auto" }}
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box sx={modalStyle}>
        <Box
          sx={{
            width: "100%",
            display: "flex",
            justifyContent: "center",
            position: "relative",
          }}
        >
          <Typography
            id="modal-modal-title"
            variant="h6"
            component="h2"
            sx={{
              fontSize: "16px",
              fontWeight: "bold",
              alignItems: "center",
              display: "flex",
            }}
          >
            {t(title)}
          </Typography>
          <button
            className="w-5 h-5 flex justify-center items-center absolute right-0 top-0"
            onClick={handleClose}
          >
            <FontAwesomeIcon icon={faTimes} className="text-xl" />
          </button>
        </Box>
        {content && (
          <Typography
            sx={{ mt: 2, fontSize: "14px", width: "100%" }}
            id="modal-modal-description"
          >
            {content}
          </Typography>
        )}

        {options && options.length > 0 && (
          <List sx={{ width: "100%" }}>
            {options.map((option) => (
              <ListItem
                key={option.id}
                onClick={() => handleOptionSelect(option)}
                sx={{ justifyContent: "center", position: "relative" }}
              >
                {option.id === selectedOption?.id && (
                  <FontAwesomeIcon
                    icon={faCheck}
                    className="text-xs mr-4 text-transparent"
                  />
                )}
                <span
                  className={`text-xs ${
                    option.id === selectedOption?.id ? "text-secondary" : ""
                  }`}
                >
                  {option.name}
                </span>
                {option.id === selectedOption?.id && (
                  <FontAwesomeIcon
                    icon={faCheck}
                    className={`text-xs ml-4 ${
                      option.id === selectedOption?.id ? "text-secondary" : ""
                    }`}
                  />
                )}
              </ListItem>
            ))}
          </List>
        )}
        {showButtons && (
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              mt: 3,
            }}
          >
            <Button
              className="button-shorted mr-5 inactive"
              //variant="outlined"
              onClick={onCancel}
            >
              {t("取消")}
            </Button>
            <Button
              className="button-shorted active"
              //variant="contained"
              onClick={() => onConfirm(selectedOption)}
            >
              {t("確認")}
            </Button>
          </Box>
        )}
      </Box>
    </Modal>
  );
}

export default BasicModal;
