import * as React from "react";
import { Link } from "react-router-dom";

import { AppDispatch, RootState } from "../../../store";
import { useDispatch, useSelector } from "react-redux";
import { fetchComicInfo } from "../../../store/comicSlice";

interface ComicCardProps {
  id: string;
  uuid: string;
  title: string;
  imageUrl: string;
  isLinkEnabled?: boolean;
  toggleSelectItem?: (index: string) => void;
  onClick?: () => void;
  mode?: string;
}

const ComicCard: React.FC<ComicCardProps> = ({ 
  id,
  uuid,
  title,
  imageUrl,
  isLinkEnabled = true, // 默認啟用連結
  toggleSelectItem,
  onClick,
  mode
}) => {

  const selectItem = useSelector((state: RootState) => state.favorites.selectItem);

  // Check if the current id exists in selectItem
  const selectedIndex = selectItem.indexOf(id);
  const isSelected = selectedIndex !== -1;

  const CardContent = (
    <>
      <div 
        className="relative aspect-[0.71]"
        onClick={() => {
          if (toggleSelectItem) {
            toggleSelectItem(id);
          }
          if (onClick) {
            onClick();
          }
        }}
      >
        {/* Overlay if item is selected */}
        {isSelected && (
          <div className="absolute inset-0 bg-gray-500 opacity-50 z-10"></div>
        )}

        {/* Display selected index in a circle at the top-right corner */}
        {isSelected && (
          <span className="absolute top-0 right-0 m-1 bg-blue-500 text-white rounded-full w-6 h-6 flex items-center justify-center z-20">
            {selectedIndex + 1}
          </span>
        )}

        <img
          loading="lazy"
          src={
            imageUrl
          }
          alt={title || "Comic thumbnail"}
          className="object-cover w-full h-full"
        />
      </div>
      {mode === 'indexLayout' ? (
        <div className='text-left mt-2 text-sm line-clamp-1'>{title}</div>
      ): (
        <div className='text-left mt-2 text-sm line-clamp-2'>{title}</div>
      )}
    </>
  );

  return isLinkEnabled ? (
    <Link 
      className="flex flex-col" 
      to={`/app/Comic`}
      state={{ uuid: uuid}}
    >
      {CardContent}
    </Link>
  ) : (
    <div className="h-full w-full flex flex-col justify-between">{CardContent}</div>
  );
};

export default ComicCard;
